[data-play-sound] {
  cursor: pointer;
}
.div-relativ_rezult {
  display: none;
}
.div-relativ_rezult--active {
  display: block;
}
.lbock_window-manager {
  max-height: 95vh;
  overflow: auto;
}
.quiz-steps {
  height: 434px;
}
.gift-block {
  margin-top: 25px;
  background: #3d4058;
  padding: 17px 9px 21px 9px;
  color: #fff;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
  position: relative;
}
.gift-block__text {
  text-align: left;
  font-weight: 800;
  font-size: 11px;
}
.progressbar-choice_zentr {
  max-width: 100%;
}
.div-selection-form {
  width: 276px;
}